/*---------------------------Upper Header-------------*/
.nav-link{
	font-family: OpenSans;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #e5e5e5;
  text-transform: uppercase;
  margin-right: 5px;
  padding: 10px 15px!important;
  -webkit-transition: ease .2s;
  transition: ease .2s;
}
.nav-link:hover, .nav-link:focus{
	background-color: #cc2027;
	color: #fff;
}
.navbar{
	padding: 0;
	background-color: #222222;
}
.active{
	background-color: #cc2027!important;
}
/*---------------------------Down Header-------------*/
.logo-box{
	background-image: -webkit-linear-gradient(bottom, #840000, #cc2027);
	background-image: linear-gradient(to top, #840000, #cc2027);
	padding: 10px 0;
}
body{
	font-family: 'Open Sans', sans-serif;
}
ul{
	list-style: none;
	padding: 0;
	margin: 0;
}
a:hover{
	text-decoration: none;
}
footer{
	text-align: center;
	padding: 25px 0 20px;
	background-color: #222222;
}
.list-title{
  font-size: 13px;
  color: #76767b;
}
.payment-list{
	margin-bottom: 40px;
}
.payment-list img{
	max-height: 35px;
}
.payment-list li{
	margin: 0 2px;
	display: inline-block;
}
.partner-list{
	max-width: 900px;
	margin-left: auto;
	margin-right: auto;
}
.partner-list li{
	display: inline-block;
	margin: 20px 8px;
}
.partner-list img{
	max-height: 80px;
}
.copy-right p{
	color: #6d6d6d;
	font-size: 12px;
	margin-bottom: 15px;
	line-height: 1.3;
}
